.Panel__in {
  background-color: #2a2a72;
  background-image: linear-gradient(0deg, #4848b6 0%, #009ffd 100%);
}
.PanelHeaderBack__in {
    background-color:white;
}
.ScreenSpinner__container {
  background-color: transparent !important;
}
.Placeholder__text {
  font-family: 'Raleway';
  color: white;
}
.Placeholder__header { 
  font-family: 'Raleway';
  color: white;
}
.spanRed {
  font-family: 'Raleway';
  color: #db0f10;
}
.spanBlue {
  font-family: 'Raleway';
  color: #027bcd;
}
.iconHeader {
  margin-top: 4vh;
  height: 30%;
  width: 30%;
}
.iconIntro {
  height: 9%;
  width: 9%;
}
.customText {
  font-family: 'Raleway';
  color: white;
}
.globalCounter {
  height: 100%;
  width: 100%;
  display: block;
  margin: 0 auto;
  border: 0.5px solid #009ffd;
  border-radius: 10px 10px 10px 10px ;
  -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  background-color: #2a2a72;
  background-image: linear-gradient(315deg, #3f3f9b 0%, #009ffd 74%);
}
.clickButtonMain {
  cursor: pointer;
  height: 100%;
  width: 100%;
  display: block;
  margin: 0 auto;
  border-radius: 50% 50% 50% 50%;
  border: 0.5px solid #009ffd;
  background-color: #2a2a72;
  -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  background-image: linear-gradient(0deg, #3f3f9b 0%, #009ffd 100%);
}
.customButtonInfo {
  cursor: pointer;
  font-family: 'Raleway';
  color: white;
  border: 0.5px solid #009ffd;
  background-color: #2a2a72;
  -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.61) inset;
  background-image: linear-gradient(0deg, #3f3f9b 0%, #009ffd 100%);
}
.clicking {
  height: 45%;
  width: 45%;
  padding-top: 10px;
}
